import React, { useState } from "react";
import {
  AppBar,
  Button,
  Grid,
  isWidthUp,
  Menu,
  MenuItem,
  Typography,
  withWidth,
} from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { getCompiledPoints, getPointsToShow } from "../../app/selectors";
import { map, startCase } from "lodash";
import { updatePointsToShow } from "../../app/choicesSlice";

const PointsMenu = ({ width }) => {
  const dispatch = useDispatch();
  const points = useSelector(getCompiledPoints);
  const pointsToShow = useSelector(getPointsToShow);
  const [isMenuOpen, toggleMenuOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const handleMenuOpenClose = (e) => {
    setAnchorEl(e.currentTarget);
    toggleMenuOpen(!isMenuOpen);
  };
  const handleMenuItemSelect = (pts) => {
    console.log(pts);
    dispatch(updatePointsToShow(pts));
    handleMenuOpenClose({ currentTarget: null });
  };

  return (
    <Grid
      container
      alignContent="center"
      style={{ height: "100%", paddingRight: "16px" }}
    >
      <Button variant="contained" onClick={handleMenuOpenClose}>
        <Typography>{`${
          isWidthUp("xs", width) ? `${startCase(pointsToShow)}: ` : null
        }${points[pointsToShow]}`}</Typography>
      </Button>
      <Menu open={isMenuOpen} onClose={handleMenuOpenClose} anchorEl={anchorEl}>
        {map(points, (numOfPoints, pointType) => {
          return (
            <MenuItem
              onClick={() => handleMenuItemSelect(pointType)}
              key={`menuItem=${pointType}`}
            >
              {`${startCase(pointType)} Points: ${numOfPoints}`}
            </MenuItem>
          );
        })}
      </Menu>
    </Grid>
  );
};

export default withWidth()(PointsMenu);
